<script>
export default {
	name: 'Result',
	data() {
		return {
			time: 0,
			inIndex: true,
			timer: null,
			date: ''
		};
	},
	methods: {
		goIndex() {
			this.time = 0;
		}
	},
	created() {
		var myDate = new Date();
		var M = 0;
		if (myDate.getMonth() < 9) {
			M = '0' + (myDate.getMonth() + 1);
		} else {
			M = myDate.getMonth() + 1;
		}
		this.date = myDate.getFullYear() + '-' + M + '-' + myDate.getDate();
	},
	mounted() {
		let that = this;
		if (sessionStorage.getItem('time')) {
			this.inIndex = false;
			clearInterval(this.timer);
			this.timer = null;
		} else {
			that.time = 30;
			sessionStorage.setItem('time', 0);
			this.timer = setInterval(() => {
				that.time--;
			}, 1000);
		}
	},
	watch: {
		time: function(newVal, oldVal) {
			if (newVal <= 0) {
				this.inIndex = false;
				clearInterval(this.timer);
				this.timer = null;
			}
		}
	}
};
</script>

