// export default router
import {
	createRouter,
	createWebHashHistory
} from 'vue-router'
import Result from '../views/Result.vue'

const routes = [
	// {
	// 	path: '/',
	// 	name: 'Result',
	// 	component: Result //首页输入身份码
	// },
	{
		path: '/',
		name: 'Result',
		component: () => import( /* webpackChunkName: "result" */ '../views/Result.vue') //身份码详情页
	},
	{
		path: '/error',
		name: 'Error',
		component: () => import( /* webpackChunkName: "error" */ '../views/Error.vue') //身份码找不到
	},
	{
		path: '/distinguish',
		name: 'Distinguish',
		component: () => import( /* webpackChunkName: "distinguish" */ '../component/distinguish/Distinguish.vue') //防伪咨询
	},
	{
		path: '/bottled',
		name: 'Bottled',
		component: () => import( /* webpackChunkName: "bottled" */ '../component/distinguish/Bottled.vue') //瓶装防伪主页
	},
	{
		path: '/pcodeone',
		name: 'Pcodeone',
		component: () => import( /* webpackChunkName: "pcodeone" */ '../component/distinguish/bottled/PMethodone.vue') //瓶装防伪方法一
	},
	{
		path: '/pcodetwo',
		name: 'Pcodetwo',
		component: () => import( /* webpackChunkName: "pcodetwo" */ '../component/distinguish/bottled/PMethodtwo.vue') //瓶装防伪方法二
	},
	{
		path: '/boxpacked',
		name: 'Boxpacked',
		component: () => import( /* webpackChunkName: "boxpacked" */ '../component/distinguish/BoxPacked.vue') //盒装防伪主页
	},
	{
		path: '/hcodeone',
		name: 'Hcodeone',
		component: () => import( /* webpackChunkName: "hcodeone" */ '../component/distinguish/boxpacked/HMethodone.vue') //盒装防伪方法一
	},
	{
		path: '/hcodetwo',
		name: 'Hcodetwo',
		component: () => import( /* webpackChunkName: "hcodetwo" */ '../component/distinguish/boxpacked/HMethodtwo.vue') //盒装防伪方法二
	},
	{
		path: '/hcodethree',
		name: 'Hcodethree',
		component: () => import( /* webpackChunkName: "hcodethree" */ '../component/distinguish/boxpacked/HMethodthree.vue') //盒装防伪方法二
	},
	{
		path: '/hcodefour',
		name: 'Hcodefour',
		component: () => import( /* webpackChunkName: "hcodefour" */ '../component/distinguish/boxpacked/HMethodfour.vue') //盒装防伪方法二
	},
	{
		path: '/hcodefive',
		name: 'Hcodefive',
		component: () => import( /* webpackChunkName: "hcodefive" */ '../component/distinguish/boxpacked/HMethodfive.vue') //盒装防伪方法二
	},
	{
		path: '/pticulars',
		name: 'Pticulars',
		component: () => import( /* webpackChunkName: "pticulars" */ '../component/Pticulars.vue') //盒装防伪方法二
	},
	{
		path: '/culture',
		name: 'Culture',
		component: () => import( /* webpackChunkName: "culture" */ '../component/Culture.vue') //企业文化
	},
	{
		path: '/quality',
		name: 'Quality',
		component: () => import( /* webpackChunkName: "quality" */ '../component/Quality.vue') //质量信息
	},
	{
		path: '/product',
		name: 'Product',
		component: () => import( /* webpackChunkName: "product" */ '../component/Product.vue') //相关产品
	},
	{
		path: '/flow',
		name: 'Flow',
		component: () => import( /* webpackChunkName: "flow" */ '../component/FlowPath.vue') //工艺流程
	},
	{
		path: '/flowdetails',
		name: 'Flowdetails',
		component: () => import( /* webpackChunkName: "flowdetails" */ '../component/FlowDetails.vue') //工艺流程详情
	},
	{
		path: '/shop',
		name: 'Shop',
		component: () => import( /* webpackChunkName: "shop" */ '../component/Shop.vue') //销售门店
	},
	{
		path: '/evaluate',
		name: 'Evaluate',
		component: () => import( /* webpackChunkName: "evaluate" */ '../component/Evaluate.vue') //建议评价
	},
	{
		path: '/report',
		name: 'Report',
		component: () => import( /* webpackChunkName: "report" */ '../component/Report.vue') //举报
	},
	{
		path: '/government',
		name: 'Government',
		component: () => import( /* webpackChunkName: "government" */ '../component/login/GovernmentLogin.vue') //政府人员登陆
	},
	{
		path: '/interior',
		name: 'Interior',
		component: () => import( /* webpackChunkName: "interior" */ '../component/login/InteriorLogin.vue') //内部人登陆
	}
]

const router = createRouter({
	history: createWebHashHistory(),
	routes
})

export default router
